body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: lightblue;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  color: black;
}

.logo {
  display: flex;
  flex-direction: column;
  height: 8vmin;
  margin-bottom: 4vmin;
  -webkit-animation: img-spin infinite 20s linear;
          animation: img-spin infinite 20s linear;
  pointer-events: none;
}

@-webkit-keyframes img-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes img-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

h1 {
  font-size: calc(11px + 2vmin);
}

label {
  flex-wrap: wrap;
  flex-direction: column;
  font-weight: bold;
}

fieldset {
  border-style: none;
}

.Input-field {
  height: 4vmin;
  background-color: bisque;
  font-weight: normal;
  font-size: calc(6px + 1.5vmin);
  padding-top: 1vmin;
}

.input-form {
  background-color: bisque;
  padding-left: 1vmax;
  padding-right: 1vmax;
}

.submit-button {
  color: lightyellow;
  border-radius: 5px;
  background: darkblue;
  -webkit-transition: 0.3s color ease-in-out, 0.3 background ease-in-out;
  transition: 0.3s color ease-in-out, 0.3 background ease-in-out;
  margin-bottom: 4vh;
  -webkit-transition: 0.2s opacity linear;
  transition: 0.2s opacity linear;
}

.submit-button:disabled {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  opacity: 0.5;
  -webkit-transition: 0.2s opacity linear;
  transition: 0.2s opacity linear;
}

.current-weather {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: -webkit-repeating-linear-gradient(
    left,
    rgba(102, 102, 204, 0.5) 10%,
    rgba(102, 102, 204, 1) 20%,
    rgba(102, 102, 204, 0.5) 30%
  );
  background: repeating-linear-gradient(
    to right,
    rgba(102, 102, 204, 0.5) 10%,
    rgba(102, 102, 204, 1) 20%,
    rgba(102, 102, 204, 0.5) 30%
  );
  font-weight: bold;
  font-size: calc(6px + 2vmin);
  color: beige;
  height: 100%;
}

ul {
  list-style: none;
}
.weather-icon {
  min-width: 1vmax;
  max-width: 3vmax;
}

.weather-icon:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.forecast-weather {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: -webkit-repeating-linear-gradient(
    left,
    rgba(102, 102, 204, 0.5) 10%,
    rgba(102, 102, 204, 1) 20%,
    rgba(102, 102, 204, 0.5) 30%
  );
  background: repeating-linear-gradient(
    to right,
    rgba(102, 102, 204, 0.5) 10%,
    rgba(102, 102, 204, 1) 20%,
    rgba(102, 102, 204, 0.5) 30%
  );
  font-weight: bold;
  font-size: calc(6px + 2vmin);
  color: beige;
  height: 100%;
}

ul {
  list-style: none;
}
.weather-icon {
  min-width: 1vmax;
  max-width: 3vmax;
}

.weather-icon:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.weather-card {
  font-weight: normal;
  font-size: calc(6px + 1vmin);
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 2px solid hsla(240, 100%, 27%, 0.5);
  box-shadow: 4px 4px hsla(195, 53%, 79%, 0.5);
  border-radius: 10px;
  margin: 1vmin;
  overflow: hidden;
}

.weather-card--header {
  color: darkred;
  font-weight: bold;
  background-color: beige;
  padding: 1vmin;
  display: inline;
}

.weather-card--list-style {
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}

